body {
    padding-left: 80px;
}

.input_section {
    display: flex;
    flex-direction: column;
    padding-bottom: 2rem;
}

.input_container {
   display: grid;
   grid-template-columns: repeat(2, 350px);
}

.input {
    border: 1px solid #828282;
    width: 200px;
    height: 56px;
    box-sizing: border-box;
    border-radius: 8px;
}

.input:hover {
    border: 1px solid #333333;
}

.input:focus {
    border: 1px solid #2962FF;
    outline: none;
    border-radius: 8px;
}

label:focus {
    color: #2962FF;
}

.container {
    display: flex;
    flex-direction: column;
    position: relative;
}

.input__error,
.input__error:focus {
    border: 1px solid #D32F2F;
    outline: none;
}

.input__label_error,
.input__label_error:focus {
    color: red;
}

.input__error:hover {
    border: 1px solid #333333;
}
.input__error:hover,
.input__label_error:hover {
    color: #333333;
}

.input:disabled {
    background: #F2F2F2;
    border: 1px solid #E0E0E0;
}

.text_interesting p:last-child, 
.text_interesting label {
    color: red;
}

.text_interesting input {
    border: 1px solid #D32F2F; 
}

.input__sm {
    width: 200px;
    height: 40px;
}

.input__fullwidth {
    width: 100%;
}

.input__row-4 {
    width: 200px;
    height: 150px;
}

.container p {
    padding: 0;
    margin: 0;
    position: absolute;
    top: 50%;
}

.container p:nth-of-type(1) {
    left: 5%;
    right: 0;
    bottom: 0;
}

.container p:last-child {
    left: 45%;    
    right: 0;
    bottom: 0;
}

.input__call::placeholder {
    padding-left: 3rem;
}

.input__lock::placeholder {
    padding-left: 1rem;
}

.input__multiline {
    height: 100px;
}